.carousel {
  height: 38vh;
  box-sizing: content-box;
  position: relative;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  overflow: hidden;
}
.carousel .carousel-slide {
  height: inherit;
}
.carousel img {
  width: 100vw;
  height: inherit;
  object-fit: cover;
}

.carousel > div {
  position: absolute;
}

.carousel-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.fade-in {
  opacity: 0;
  transition: opacity 250ms ease-in;
}

.fade-in.appear {
  opacity: 1;
}

.from-left {
  grid-column: 2/3;
  transform: translateX(-50%);
}

.from-right {
  grid-column: 3/4;
  transform: translateX(50%);
}

.from-left,
.from-right {
  transition: opacity 250ms ease-in, transform 600ms ease-in;
  opacity: 0;
}

.from-left.appear,
.from-right.appear {
  transform: translateX(0);
  opacity: 1;
}

